/* Fonts */

body {
 	font-family: 'Montserrat', sans-serif;
 	color: #fff;
 	font-size: 18px;
}


/* /Fonts */

/* Document */

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	margin: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  -webkit-animation: gradientBG 15s ease infinite;
          animation: gradientBG 15s ease infinite;
}

@-webkit-keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -10;
}

:focus {
  outline: none;
}

a {
  text-decoration: none;
  color:#fff;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

/* /Document */

/* Common */

.centered {
  display: flex;
  flex-flow: row nowrap;
  justify-content:  center;
  align-content: center;
  align-items: center;
}

.relative {
  position: relative;
}

.w-100 {
  width: 100%;
}

.wrapper {
  max-width: 1200px;
}


/*Button styled by Alex Loomer - CODEPEN link - https://codepen.io/atloomer/pen/JEaRWX */
.button {
  display: inline-flex;
  height: 40px;
  width: 150px;
  border: 2px solid #fff;
  margin: 20px 20px 20px 20px;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 1em;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  box-sizing: border-box;
}

.button-circle {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.button-circle a {
  position: relative;
  -webkit-transition: all .45s ease-Out;
  transition: all .45s ease-Out;
}

.button-circle__circle {
  width: 0%;
  height: 0%;
  opacity: 0;
  line-height: 40px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  -webkit-transition: all .5s ease-Out;
  transition: all .5s ease-Out;
  top: 20px;
  left: 70px;
}

.button-circle:hover .button-circle__circle {
  width: 200%;
  height: 500%;
  opacity: 1;
  top: -70px;
  left: -70px;
}

.button-circle:hover a {
  color: #2D3142;
}

.img {
  max-width: 100%;
  height: auto;
  max-height: 550px;
  border: 1px solid white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.img__container {
  max-width: 50%;
  margin: 0 20px;
}

@media screen and (max-width: 425px) {
  .img__container {
  max-width: 100%;
  }
}
/* Common */

/* Navigation */

.nav {
	display:  flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-content: center;
  align-items: center;
  margin: auto;
  margin-bottom: 20px;
}

.bordered {
	border: 1px solid #fff;
}

.logo {
  margin: 20px;
  font-family: 'Pacifico', cursive;
  font-size: 1.5em;
}

.logo:hover {
  cursor: pointer;
}

.logo {
  -webkit-animation: tracking-in-contract 0.8s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
          animation: tracking-in-contract 0.8s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

